import Base from '../Base';
import {
  render,
  fireEvent,
  screen,
  waitFor,
} from '@testing-library/react-native';
import React from 'react';
import Geolocation from 'react-native-geolocation-service';
import {useDispatch} from 'react-redux';
import {NativeStackNavigationProp} from '@react-navigation/native-stack';
import {useNavigation} from '@react-navigation/native';
import '@testing-library/jest-dom';
jest.mock('react-redux', () => ({
  useDispatch: jest.fn(),
}));
jest.mock('react-native-geolocation-service', () => {
  return {
    getCurrentPosition: jest.fn((success, error, options) => {
      const location = {
        coords: {
          latitude: 17.3920466,
          longitude: 78.4758037,
        },
      };
      success(location);
    }),
  };
});
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

describe('Locations List Screen/Base Component', () => {
  const mockDispatch = jest.fn();
  let useDispatchMock: jest.Mock;
  beforeEach(() => {
    useDispatchMock = useDispatch as jest.Mock;
    useDispatchMock.mockReturnValue(mockDispatch);
  });

  it('renders the base component correctly', () => {
    render(<Base />);
    const currentLabel = screen.getByTestId('list-current-label');
    expect(currentLabel.props.children).toBe('Current Location');
  });
  it('should have the right location', () => {
    render(<Base />);
    expect(Geolocation.getCurrentPosition).toHaveBeenCalled();
  });
  it('renders the current location', async () => {
    const currentLocation = {
      address: '123 Main St',
      time: '2023-03-01T13:00:00.000Z',
      latitude: 42.123456,
      longitude: -71.123456,
    };
    const {getByTestId} = render(<Base />);
    const currentLocationTime = await waitFor(() =>
      getByTestId('list-current-time'),
    );
    expect(currentLocationTime).toHaveTextContent(currentLocation.time);
  });
});
